import React, { useEffect, useRef, useState } from "react";
import { Container, Row, Col, Card, Form, Button } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import { IoIosArrowForward } from "react-icons/io";
import { Link, useLocation, useParams } from "react-router-dom";
import { PATH } from "../../../utils/path";
import {
  createInbox,
  getAllMessage,
  getInboxId,
  pushSendMessade,
  sendMessage,
} from "../../../app/features/chat/chat.slice";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import { convertTimeIntoUTC } from "../../../components/GenericActions";
import { socket } from "../../../authorization/ProtectedRoute";

function StaffChat() {
  const location = useLocation();
  const param = useParams();
  const bottomRef = useRef(null);
  const inputRef = useRef(null);
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);
  const [newMessage, setNewMessage] = useState("");
  const { inboxId, allMessages } = useSelector((state) => state?.chat);
  const { staffData } = location?.state;
  const userId = user?.userId;

  useEffect(() => {
    if (socket) {
      socket?.on("connect", () => {
        console.log("signal1", socket?.connected);
      });
      socket?.emit("connectToUserRoom", { userId: userId }, () => {
        console.log("signal2", socket?.connected);
        // setAgainCheckStatus(true);
      });
      if (socket?.connected === false) {
        socket?.on("reconnect", () => {
          console.log("reconnect", socket?.connected);
        });
      }
      if (socket?.connected === true) {
        console.log("refttytfconnect", socket?.connected);
      }
    }
  }, [staffData, socket]);

  useEffect(() => {
    if (socket?.connected === true) {
      socket?.on("messageData", (data) => {
        if (data?.data) {
          const jsonData = data?.data?.data;
          const senderMessage = JSON.parse(jsonData);
          const finalData = {
            createdDate: convertTimeIntoUTC(new Date()),
            file: senderMessage?.file ? senderMessage?.file : null,
            fileUrl: senderMessage?.fileUrl ? senderMessage?.fileUrl : null,
            fromUserId: senderMessage?.fromUserId,
            fromUserName: senderMessage?.fromUserName
              ? senderMessage?.fromUserName
              : staffData?.name
              ? staffData?.name
              : null,
            image: senderMessage?.image ? senderMessage?.image : null,
            imageUrl: senderMessage?.imageUrl ? senderMessage?.imageUrl : null,
            inboxId: senderMessage?.inboxId,
            message: senderMessage?.message,
            messageId: senderMessage?.messageId
              ? senderMessage?.messageId
              : null,
            toUserId: senderMessage?.toUserId,
            toUserName: senderMessage?.toUserName
              ? senderMessage?.toUserName
              : null,
          };
          dispatch(pushSendMessade(finalData));
        }
      });
    }
  }, [socket, inboxId]);

  const handleNewMessageChange = (event) => {
    setNewMessage(event.target.value);
  };
  const handleKeyDown = (e) => {
    if (e.which === 32 && inputRef.current.selectionStart === 0) {
      e.preventDefault();
    }
  };
  const moveToNext = (data) => {
    socket.emit("sendMessage", {
      userIds: [
        {
          id: +param?.staffId,
        },
      ],
      data: JSON.stringify(data?.data),
      type: "inteli_health",
      // uuid: v4(),
    });
  };

  const movetoNext = (data) => {
    const finalData = {
      inboxId: data?.data?.inboxId,
      fromUserId: user?.userId,
      toUserId: +param?.staffId,
      message: newMessage ? newMessage : "",
      isChat: true,
    };
    dispatch(sendMessage({ finalData, moveToNext }));
  };
  const handleNewMessageSubmit = (event) => {
    if (inboxId) {
      const finalData = {
        inboxId: inboxId,
        fromUserId: user?.userId,
        toUserId: +param?.staffId,
        message: newMessage ? newMessage : "",
        isChat: true,
      };
      dispatch(sendMessage({ finalData, moveToNext }));
    } else {
      const finalData = {
        fromUserId: user?.userId,
        toUserId: +param?.staffId,
      };
      dispatch(createInbox({ finalData, movetoNext }));
    }
    event.preventDefault();
    setNewMessage("");
  };

  useEffect(() => {
    const finalData = {
      inboxId: inboxId ? inboxId : 0,
    };
    dispatch(getAllMessage(finalData));
  }, [dispatch, inboxId]);

  useEffect(() => {
    const finalData = {
      fromUserId: user?.userId,
      toUserId: +param?.staffId,
    };
    dispatch(getInboxId(finalData));
  }, [dispatch, user?.userId, param?.staffId]);

  useEffect(() => {
    // 👇️ scroll to bottom every time messages change
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [allMessages]);

  return (
    <>
      <nav aria-label="breadcrumb">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
            <Link
              to={{
                pathname: PATH.STAFF,
              }}
              className="text-decoration-none fs-5"
              style={{ color: "#999999" }}
            >
              Staff
            </Link>
          </li>
          <IoIosArrowForward className="ms-2 mt-2" />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#6045EB" }}
          >
            Chat
          </li>
        </ol>
      </nav>
      <div className="Staff-Chat-MainClass">
        <Card className="h-100 ">
          <Card.Body className="py-0 px-0">
            <Card.Title className="mb-0">
              <Container fluid className="">
                <Row>
                  <Col
                    xl={12}
                    lg={10}
                    md={12}
                    className="px-0 chatContent"
                    style={{ boxShadow: "0 0 8px #C3C3C3" }}
                  >
                    <div className="d-flex justify-content-between mobileFlex_Col py-2 border-bottom px-4 ">
                      <div className="d-flex justify-content-center align-items-center chatActive mb-sm-0 mb-3">
                        <div className="d-flex align-items-center py-0">
                          <span>
                            <img
                              className="rounded-circle"
                              width={50}
                              src={
                                staffData?.imageUrl
                                  ? staffData?.imageUrl
                                  : `https://ui-avatars.com/api/?name=${`${staffData?.name}`}&background=6045eb&color=fff`
                              }
                              alt="UserImage"
                            />
                          </span>
                          <h5 className="fw-semibold mb-0 ms-3 mobileTextHeading">
                            {staffData?.name}
                          </h5>
                        </div>
                      </div>
                      {/* <div className="d-flex justify-content-end align-items-center">
                        <span>
                          <img
                            className="me-4"
                            src={IMAGES.CHAT_CALL_ICON}
                            alt="call icon"
                          />
                        </span>
                        <span>
                          <img
                            src={IMAGES.CHAT_VIDEO_ICON}
                            alt="video call icon"
                          />
                        </span>
                      </div> */}
                    </div>
                    <div>
                      <div
                        className="div-fixed-height"
                        style={{ background: "#FBFBFB" }}
                      >
                        {allMessages.length > 0 ? (
                          allMessages?.map((chatData, index) => {
                            return (
                              <>
                                <div className="d-flex flex-column h-100 px-4 py-3 chatBox">
                                  <div>
                                    {allMessages?.[
                                      index - 1
                                    ]?.createdDate?.split("T")?.[0] !==
                                      chatData?.createdDate?.split(
                                        "T"
                                      )?.[0] && (
                                      <div className="text-center rounded-pill mx-auto chatSpan bg-white">
                                        {moment(chatData?.createdDate).format(
                                          "DD-MM-YYYY"
                                        ) ===
                                        moment(new Date()).format("DD-MM-YYYY")
                                          ? "Today"
                                          : moment(
                                              chatData?.createdDate
                                            ).format("DD-MM-YYYY")}
                                      </div>
                                    )}

                                    <div>
                                      <Row>
                                        {chatData?.fromUserId !==
                                        user?.userId ? (
                                          <Col
                                            lg={8}
                                            md={6}
                                            className="d-flex flex-column justify-content-start"
                                          >
                                            <div className="chatMessage_left d-flex justify-content-start align-items-center mobileFlex_Col mb-1">
                                              <img
                                                className="me-2 mb-2 mb-sm-0 rounded-circle"
                                                style={{ width: "4%" }}
                                                src={
                                                  chatData?.imageUrl
                                                    ? chatData?.imageUrl
                                                    : `https://ui-avatars.com/api/?name=${chatData?.fromUserName}&background=6045eb&color=fff`
                                                }
                                                alt="UserImage"
                                              />
                                              <span className="messageSpan">
                                                {chatData?.message}
                                              </span>
                                            </div>
                                            <span className="ms-5 chatTime">
                                              {moment(
                                                chatData?.createdDate
                                              ).format("HH:MM A")}
                                            </span>
                                          </Col>
                                        ) : (
                                          <Col
                                            lg={4}
                                            md={6}
                                            className="chatMessage_right text-white d-flex flex-column align-items-end justify-content-end w-100"
                                          >
                                            <span className="mb-1 mt-3 mobile_margin sender-message">
                                              {chatData?.message}
                                            </span>
                                            <div className="chatTime align-self-end">
                                              {moment(
                                                chatData?.createdDate
                                              ).format("HH:MM A")}
                                            </div>
                                          </Col>
                                        )}
                                      </Row>
                                    </div>
                                  </div>
                                </div>
                                <div ref={bottomRef} />
                              </>
                            );
                          })
                        ) : (
                          <h5 className="mb-0 text-center text-black-50 d-flex justify-content-center align-items-center h-100">
                            Welcome to our chat system <br /> Please feel free
                            to start typing your message
                          </h5>
                        )}
                      </div>

                      <div className="border-top">
                        <div className="d-flex justify-content-between align-items-center px-4">
                          <div className="d-flex justify-content-center">
                            {/* <Button className="bg-transparent border-0">
                              <img
                                className=""
                                src={IMAGES.SMILEYS_ICON}
                                alt="smileys"
                              />
                            </Button> */}

                            <Button
                              className="bg-transparent border-0"
                              type="file"
                            >
                              <img
                                className=""
                                src={IMAGES.CLIP_ICON}
                                alt="clip"
                              />
                            </Button>
                          </div>

                          <div className="w-100">
                            <Form.Group className="position-relative py-3  w-100">
                              <Form.Control
                                className="padding rounded-pill"
                                placeholder="Type a message here…"
                                value={newMessage}
                                onChange={handleNewMessageChange}
                                ref={inputRef}
                                onKeyDown={handleKeyDown}
                              />
                              <Button
                                className="sendIcon rounded-circle"
                                onClick={handleNewMessageSubmit}
                                disabled={newMessage === "" ? true : false}
                              >
                                <img
                                  className=""
                                  src={IMAGES.SEND_ICON}
                                  alt="messageIcon"
                                />
                              </Button>
                            </Form.Group>
                          </div>
                          {/* <Button className="bg-transparent border-0">
                            <img
                              className="ms-1"
                              type="button"
                              src={IMAGES.MIC_ICON}
                              alt="mic"
                            />
                          </Button> */}
                        </div>
                      </div>
                    </div>
                  </Col>
                </Row>
              </Container>
            </Card.Title>
          </Card.Body>
        </Card>
      </div>
    </>
  );
}

export default StaffChat;
